import React, { Component, useEffect, useState } from 'react';
import {Routes,Route, Link} from 'react-router-dom';

//
import App from '../App';//
import Check from './Check';//
import ParView from './ParView';//
import Login from './Login';//
import Logout from './Logout';
import Edit from './Edit';
import Type from './Type';


import '../css/menu.css';
import Test from './Test';

function Menu(){
    const [LoginLabel,setLoginLabel]=useState("");
    const [isLogin,setIsLogin]=useState(false);
   

    useEffect(()=>{
        if(localStorage.getItem("LoginUserName")===null||localStorage.getItem("LoginUserId")===null){
            setLoginLabel("");
        }else{
            var authorName=localStorage.getItem("LoginUserName").toString();
            var authorId=localStorage.getItem("LoginUserId").toString();
            
            var label=`${authorName}(${authorId}),`;
            setLoginLabel(label);
            setIsLogin(true);
        
        }
        
    },[])
    //

    function ShowMenu(){
        if(isLogin){
            return(<>
                <div className='bg-gradient-to-r from-amber-900 '>
                    <ul style={{width:'auto'}}>
                        <li className={`menuOptions ml-10 ${(isLogin)?'':'invisible'}`}><Link to="/"></Link></li>
                        <li className={`menuOptions ml-10 ${(isLogin)?'':'invisible'}`}><Link to="/Check"></Link></li>
                        <li className={`menuOptions ml-10`}><Link to="/Type"></Link></li>
                        
                        <li className={`menuOptions ml-10 ${(isLogin)?'':'invisible'}`} ><Link to="/Logout"></Link></li>
                        <li className='authorSpan'><span>{LoginLabel}</span></li>
                    </ul>
                </div>
            </>)
        }else{// 
            return(<>
                <div className='bg-gradient-to-r from-amber-900 '>
                    <ul style={{width:'auto'}}>
                        <li className={`menuOptions ml-10 ${(isLogin)?'invisible':''}`} ><Link to="/Login"></Link></li>
                        <li className={`menuOptions ml-10`}><Link to="/Type"></Link></li>
                        <li className='authorSpan'><span>{LoginLabel}</span></li>
                    </ul>
                </div>
            </>)
        }
    }
    


    return(<>
        <div className='menu'>
            <ShowMenu/>
            <Routes>
                <Route path="/" element={<App/>} />
                <Route path="/Check" element={<><Check/></>} />
                <Route path="/ParView" element={<><ParView /></>} />
                <Route path="/Login" element={<><Login/></>} />
                <Route path="/Logout" element={<><Logout/></>} />
                <Route path="/Edit" element={<><Edit /></>} />
                <Route path="/Type" element={<><Type/></>} />
                <Route path="/Test" element={<><Test/></>} />
            </Routes>
        </div>
    </>)
}

export default Menu;