import React, { Component } from 'react';
import { useEffect,useState } from 'react';
import '../css/App.css';
import axios from "axios";
import Editor from 'ckeditor5-custom-build/build/ckeditor';
import {CKEditor} from '@ckeditor/ckeditor5-react';
import { useLocation } from 'react-router-dom';


function Edit(){
    const location=useLocation();
    const {id,author}=location.state;
    const [parTitle,setParTitle]=useState("");
    const [hint,setHint]=useState("");
    
    const [parData,setParData]=useState();
    const [parContent,setParContent]=useState("");
    const [parDate,setParDate]=useState("");
    const user=localStorage.getItem("LoginUserId");
    

    useEffect(()=>{

        //
        if(user!==author){
            alert("");
            window.location.href="http://localhost:3000/editor/Check";
            return;
        }

        const search={
            id:id,
            author:author,
            commentNum:3
        }

        axios.post("http://localhost:5000/par/search", search).then((response) => {
            if(response.data.length===0){
                console.log('No data Found');
            }else{
                console.log(response.data);
                setParData(response.data[0]);
                //
                setParContent(response.data[0].content);
                //
                setParTitle(response.data[0].title);
    
                setParDate(response.data[0].createDate);
                //setParContent,setMoreEnable,setCommentList,setCommentNum,
            }
        });
    },[])

    


    function btnClickHandle(){
            //const current=new Date();
            
            //
            let json={
                id:id,//ID
                author:user,
                title:parTitle,
                createDate:parDate,//
                content:parContent,
            }
            
            console.log(json);

            
            axios.post("http://localhost:5000/par/Edit", json).then((response) => {
                if(response.data==="Ok200"){
                    setHint("!");
                }else{
                    setHint("");
                }

                
            });
       }


      return (<>
        <div className='main'>
            <div className="App">
                <div className='title'>
                    <input type='text' onChange={(e)=>setParTitle(e.target.value)} placeholder='' value={parTitle} required/>
                </div>
                <CKEditor 
                  editor={ Editor }
                  data={parContent}
                  onInit={ editor => {
                      // You can store the "editor" and use when it is needed.
                      const data = editor.getData();
                      setParData(data);
                      console.log( 'Editor is ready to use!', editor );
                  } }
                  onChange={ ( event, editor ) => {
                      const data = editor.getData();
                      setParContent(data);
                      //console.log( { event, editor, data } );
                     
                      
                  }}
                  
                  onBlur={ ( event, editor ) => {
                      //console.log( 'Blur.', editor );
                  } }
                  onFocus={ ( event, editor ) => {
                      //console.log( 'Focus.', editor );
                  } }

                  onReady={(editor) => {
                    editor.ui.view.editable.element.style.minHeight = "500px";
                 }}/>
                
                <div className='button'>
                    <button onClick={btnClickHandle} className='bg-red-800 rounded-md w-20 text-white font-bold'></button>
                </div>
                <div className='hint' style={{display:`${hint===""?"none":"block"}`}}>
                    <span className='text-white'>{hint}</span>
                </div>
               
            </div>
           
        </div>
            
        
    </>);
}

export default Edit;